import React,{Component} from 'react';
import Header from '../Header';
import './details.css';
import axios from 'axios';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import MenuDisplay from'./menuDisplay';




const url = "https://zomatonovapi.herokuapp.com/details"
const menu = "https://zomatonovapi.herokuapp.com/menu"

class Details extends Component {
    constructor(){
        super()

        this.state={
            details:'',
            menuList:'',
            userItem:''
        }
    } 

    addToCart = (data) => {
        console.log("data in card",data)
        this.setState({userItem:data})
    }     

    proceed = () => {
        sessionStorage.setItem('menu', this.state.userItem);
        this.props.history.push(`/placeOrder/${this.state.details.restaurant_name}`)
    }
    
    render(){
        //console.log(">>>>>>details state",this.state.userItem)
        //let details = this.state.details
        let {details} = this.state
        return(
            <>
            <Header/>
            <br/>
            <div className="container">
                <div className="panel panel-danger">
                    <div className="panel-heading">
                        <h3>{this.state.details.restaurant_name}</h3>
                    </div>
                    <div className="panel-body">
                        <img src="/images/Food.jpg" alt="" className="sliderImage"/>
                    </div>
                    <button className="btn btn-success galbutton" data-toggle="modal" data-target="#myGal">Click to see Image Gallery</button>
                    <div id="myGal" class="modal fade" role="dialog">
                    <div class="modal-dialog">
                        <div class="modal-content">
                        <div class="modal-header">
                            <button type="button" class="close" data-dismiss="modal">&times;</button>
                            <h4 class="modal-title">Modal Header</h4>
                        </div>
                        <div class="modal-body">
                            <p>Some text in the modal.</p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
                        </div>
                        </div>
                    </div>
                    </div>
                    <hr/>
                    <h2>{details.restaurant_name}</h2>
                    <h2>{details.rating_text}</h2>
                    <h2>{details.average_rating} Star</h2>
                    <Tabs>
                        <TabList>
                        <Tab>Details</Tab>
                        <Tab>Contacts</Tab>
                        <Tab>Menu</Tab>
                        </TabList>

                        <TabPanel>
                        <p>{details.restaurant_name} is a business that prepares and serves food and drinks to customers Meals are generally served and eaten on the premises, but many restaurants also offer take-out and food delivery services. Restaurants vary greatly in appearance and offerings, including a wide variety of cuisines and service models ranging from inexpensive fast food restaurants and cafeterias, to mid-priced family restaurants, to high-priced luxury establishments.</p>
                        </TabPanel>
                        <TabPanel>
                        <h2>Mobile No: {details.contact_number}</h2>    
                        <h2>Adderss: {details.address}</h2>
                        </TabPanel>
                        <TabPanel>
                        <h2>Menu</h2>
                        <MenuDisplay menudata={this.state.menuList} 
                        restId={this.props.match.params.restId}
                        finalOrder={(data) => {this.addToCart(data)}}/>

                        </TabPanel>
                    </Tabs>
                    <button className="btn btn-success" onClick={this.proceed}>Proceed</button>
                  
                </div>
            </div>
            </>
        )
    }
    //api call 
    async componentDidMount(){
        let restId = this.props.match.params.restId;
        let response = await axios.get(`${url}/${restId}`);
        let menudata = await axios.get(`${menu}/${restId}`);
        console.log(response.data)
        console.log(menudata.data)
        this.setState({details:response.data[0],menuList:menudata.data})
    }
}

export default Details;